<template>
  <div>
    <div id="general-chart"></div>
    <div class="detail">
      <table class="ui definition compact table">
        <thead>
          <tr>
            <th class="center aligned"></th>
            <th class="center aligned">姓名</th>
            <th class="center aligned">得分 / 满分{{cm.totalScore}}分</th>
            <th class="center aligned">提交时间</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(s, index) in sortedStudent">
            <td class="center aligned">{{index + 1}}</td>
            <td class="center aligned" :title="'id: ' + s.id">{{s.name}}</td>
            <td class="center aligned" :class="s.status===statusReviewed?'':'disabled'">{{scoreDisplay(s)}}</td>
            <td class="center aligned" :class="s.submitTime?'':'disabled'">{{timeDisplay(s)}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import chart from '@/etc/echarts'

import { mapState, mapGetters } from 'vuex'
import { CM_RECORD_STATUS_REVIEWED, CM_RECORD_STATUS_SUBMITTED } from 'assets/js/consts/common'

export default {
  name: 'general-stat',
  data() {
    return {
      statusReviewed: CM_RECORD_STATUS_REVIEWED,
      chart: null,
    }
  },
  mounted() {
    if (!this.cm.totalScore || (this.submitted.length + this.unsubmitted.length) === 0) return
    // 有总分的时候显示分布
    chart.then(([echarts]) => {
      const chartOption = {
        backgroundColor: '#394056',
        title: {
          text: '得分分布图',
          textStyle: { fontWeight: 'normal', fontSize: 14, color: '#F1F1F3' },
          left: 'center',
          subtext: 'x 轴根据总分平均分为10段, 未提交的学生不计算在内。',
          subtextStyle: { align: 'right' },
        },
        tooltip: {
          trigger: 'item',
          formatter: '{c0}人',
          axisPointer: { lineStyle: { color: '#57617B' } },
        },
        grid: {
          top: '20%',
          left: '2%',
          right: '8%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: [{
          type: 'category',
          name: '分数',
          axisLine: { lineStyle: { color: '#888' } },
          data: this.xAxisScoreData,
          axisTick: { show: false },
        }],
        yAxis: [{
          type: 'value',
          minInterval: 1,
          max: this.submitted.length + this.unsubmitted.length,
          axisTick: { show: false },
          name: '人数',
          axisLine: { lineStyle: { color: '#888' } },
          axisLabel: {
            margin: 10, textStyle: { fontSize: 14 },
          },
          splitLine: { lineStyle: { color: '#57617B' } },
        }],
        series: [{
          type: 'bar',
          barWidth: '45%',
          label: { normal: { show: true, position: 'top', color: '#fff', formatter: '{c}人' } },
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{ offset: 0, color: '#007CC6' }, { offset: 1, color: 'rgba(0, 255, 230, .6)' }]),
              barBorderRadius: [50, 50, 0, 0],
            },
            emphasis: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{ offset: 0, color: '#007CC6' }, { offset: 0.5, color: 'rgba(0, 255, 230, .6)' }]),
              barBorderRadius: [50, 50, 0, 0],
            },
          },
          data: this.scoreDistribution,
        }],
      }
      this.chart = echarts.init(document.getElementById('general-chart'))
      this.chart.setOption(chartOption)
    })
  },
  computed: {
    sortedStudent() {
      return _.orderBy(this.allStudents, ['score', 'submitTime'], ['desc', 'asc'])
    },
    xAxisScoreData() {
      const fragment = Math.round(this.cm.totalScore / 10)
      const data = []
      for (let step = 0; step < 10; step++) {
        if (step === 9) {
          data.push(`[${step * fragment}, ${this.cm.totalScore}]`)
        } else {
          data.push(`[${step * fragment}, ${(step + 1) * fragment})`)
        }
      }
      return data
    },
    scoreDistribution() {
      const fragment = Math.round(this.cm.totalScore / 10)
      const data = []
      for (let step = 0; step < 10; step++) {
        data.push({ value: 0, students: [] })
      }
      for (const s of this.sortedStudent) {
        if (s.score < 0 || s.status !== CM_RECORD_STATUS_REVIEWED) continue
        const pos = Math.floor(s.score / fragment)
        if (pos === 10) {
          data[9].value++
          data[9].students.push(s.name)
        } else {
          data[pos].value++
          data[pos].students.push(s.name)
        }
      }
      for (const item of data) {
        if (item.value > 0) {
          item.label = { normal: { show: true, position: 'top', color: '#fff', formatter: '{c}人' } }
          const groupLength = 8
          const nGroup = Math.ceil(item.value / groupLength)
          let formatter = `${item.value}人`
          for (let step = 0; step < nGroup; step++) {
            formatter += '<br>'
            formatter += item.students.slice(step * groupLength, (step + 1) * groupLength).join('，')
          }
          item.tooltip = { formatter }
        } else {
          item.label = { normal: { show: false } }
        }
      }
      return data
    },
    ...mapState(['cm']),
    ...mapGetters(['allStudents', 'unsubmitted', 'submitted', 'submittedUnreviewed', 'submittedReviewed']),
  },
  methods: {
    timeDisplay(s) {
      if (!s.submitTime && s.status === CM_RECORD_STATUS_REVIEWED) {
        return '强制收卷'
      }
      if (!s.submitTime) {
        return '未提交'
      }
      return s.submitTime.replace('T', ' ').slice(0, 16)
    },
    scoreDisplay(s) {
      if (s.status === CM_RECORD_STATUS_REVIEWED) {
        return s.score
      }
      if (s.status === CM_RECORD_STATUS_SUBMITTED) {
        return '未批改'
      }
      return '-'
    },
  },

}
</script>

<style lang="less" scoped>
.ui.table {
  width: 600px;
  margin: 1rem;
}

.detail {
  padding-top: 4rem;
  padding-bottom: 4rem;
}
#general-chart {
  height: 400px;
  margin: -1rem;
  margin-top: ~'calc(-1rem - 1px)';
  padding: 1rem;
}
</style>



