<template>
  <div>
    <div class="paper-container" v-if="viewMode===1 && questions.length">
      <div class="view-setting">
        <input type="checkbox" name="" id="only-subjective" v-model="onlySubjective"><label for="only-subjective">只看主观题</label>
      </div>
      <template v-for="(question, idx) of questions">
        <component
          :is="currentView(question)"
          v-bind="{index: idx + 1, question: question, allStudents: allStudents, showStat: true}"
          :key="question.key"
          v-show="!onlySubjective || question.isSubjective"></component>
        <div class="ui divider" v-show="!onlySubjective || question.isSubjective"></div>
      </template>
    </div>
    <div class="paper-view-by-student"  v-if="viewMode===2 && questions.length">
      <div class="student-selection" v-if="">
        <div class="ui middle aligned selection list" v-if="submittedReviewed.length">
          <div class="header header-lb">已批改</div>
          <student-labels
            :orderedResults="submittedReviewed"
            :selectedItem="selectedStudent"
            @selected="s => selectedStudent = s"></student-labels>
        </div>
        <div class="ui middle aligned selection list" v-if="submittedUnreviewed.length">
          <div class="header header-lb">提交未批改</div>
          <student-labels
            :orderedResults="submittedUnreviewed"
            :selectedItem="selectedStudent"
            @selected="s => selectedStudent = s"></student-labels>
        </div>
        <div class="ui middle aligned selection list" v-if="unsubmitted.length">
          <div class="header header-lb">未提交</div>
          <student-labels
            :orderedResults="unsubmitted" :active="false"></student-labels>
        </div>
      </div>
      <div class="paper-container" v-if="selectedStudent">
        <div class="student-report-heading">
          <h1 class="student-report-title">{{selectedStudent.name}} 的答题报告</h1>
          <p v-if="selectedStudent.score < 0">得分 待批改 /{{cm.totalScore}} 分</p>
          <p v-else>得分 <span class="student-score">{{selectedStudent.score}}</span> /{{cm.totalScore}} 分</p>
        </div>
        <div class="view-setting">
          <template v-if="cm.subjectId === 1">
            <input type="checkbox" name="" id="answer-sheet" v-model="answerSheetMode"> <label for="answer-sheet">以答题卡模式查看（隐藏大题题干和解析）</label>
          </template>
          <input type="checkbox" name="" id="only-subjective" v-model="onlySubjective"> <label for="only-subjective">只看主观题</label>
          <span class="ui tooltip link link-primary" data-tooltip="按学生查看，学生若未选中正确答案，则正确答案的选项会以下划线标识出。" data-position="bottom right"> 展示说明&gt;&gt; </span>
        </div>
        <template v-for="(question, idx) of questions">
          <component
            :is="currentView(question)"
            v-bind="{index: idx + 1, question: question, allStudents: allStudents, showStat: false, studentId: selectedStudent.id, showUserAnswer: true, answerSheetMode: answerSheetMode}"
            :key="question.key"
            v-show="!onlySubjective || question.isSubjective"></component>
          <div class="ui divider" v-show="!onlySubjective || question.isSubjective"></div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState, mapGetters } from 'vuex'
import { getQuestionByData, QuestionKindToMV } from 'assets/js/questions/view-mode'
import StudentLabels from 'components/student-labels'

const components = {}
for (const item of Object.values(QuestionKindToMV)) {
  components[item.viewName] = item.view
}

export default {
  name: 'paper',
  props: {
    viewMode: { type: Number, default: 1 },
  },
  data() {
    return {
      questions: [],
      questionsLookUp: {},
      selectedStudent: null,
      answerSheetMode: false,
      onlySubjective: false,
    }
  },
  created() {
    this.fetchPaperQuestions().then(() => { this.attachPaperReport() })
  },
  mounted() {
    if (this.submitted && this.submitted.length) {
      this.selectedStudent = this.submitted[0]
    }
  },
  components: {
    StudentLabels,
    ...components,
  },
  computed: {
    ...mapState(['cm']),
    ...mapGetters(['allStudents', 'totalStuCount', 'unsubmitted', 'submitted', 'submittedUnreviewed', 'submittedReviewed']),
  },
  watch: {
    cm(val, oldVal) {
      if (val.id !== oldVal.id) {
        this.fetchPaperQuestions().then(() => { this.attachPaperReport() })
      } else {
        this.attachPaperReport()
      }
      if (this.selectedStudent) {
        this.$nextTick(() => {
          for (const s of this.submitted) {
            if (s.id === this.selectedStudent.id) {
              this.selectedStudent = s
            }
          }
        })
      }
    },
  },
  methods: {
    currentView(question) {
      if (!QuestionKindToMV) return ''
      return QuestionKindToMV[question.kind].view
    },
    fetchPaperQuestions() {
      return axios.get('/pad/j/fetch/paper/questions', { params: { paperId: this.cm.subTargetId } }).then(
        (response) => {
          this.questions = []
          this.questionsLookUp = {}
          for (const questionData of response.data.questions) {
            const q = getQuestionByData(questionData)
            this.questionsLookUp[q.key] = q
            this.questions.push(q)
          }
        },
      )
    },
    attachPaperReport() {
      return axios.get('/pad/j/coursematerial/report', { params: { courseMaterialId: this.cm.id, subTargetKind: 2018 } }).then(
        (response) => {
          for (const reportData of response.data.report) {
            const key = `key-${reportData.kind}-${reportData.id}`
            if (this.questionsLookUp.hasOwnProperty(key)) {
              this.questionsLookUp[key].injectReportData(reportData)
            }
          }
        })
    },
  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';

.paper-container {
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  line-height: 1.8;
  height: 100%;
  font-size: 14px;
  // -webkit-font-smoothing: auto;
  // color: #555;
}

.paper-container .divider:last-child {
  display: none;
}

.ui.label.tooltip {
  border: none;
}


</style>



