<template>
<div>
  <div class="file-container" v-if="nq && nq.fileType">
    <component
      v-show="!showAnalysisFile"
      :is="getCmpNameByFileType(nq.fileType)"
      :file-url="nq.fileUrl"
      :key="'nq-file-' + nq.id"
      ></component>
    <template v-if="nq.analysisFileType && analysisFileInitialized">
      <component
        v-show="showAnalysisFile"
        :is="getCmpNameByFileType(nq.analysisFileType)"
        :file-url="nq.analysisFileUrl"
        :key="'nq-analysis-file-' + nq.id"
        ></component>
    </template>
  </div>
  <div class="ui toggle checkbox" :class="!hasAnalysisFile && 'disabled'">
    <input type="checkbox" tabindex="0" class="hidden" v-model="showAnalysisFile" :disabled="!hasAnalysisFile && 'disabled'">
    <label>{{hasAnalysisFile && '查看解析文件' || '无解析文件'}}</label>
  </div>
  <div class="nq-view-by-question">
    <div class="nq-container" v-if="viewMode===1 && questions.length">
      <template v-for="question of questions">
        <nq-objective v-if="question.kind === 2026" class="nq-objective-wrapper"
          v-bind="{question: question, allStudents: allStudents, showStat: true}"
          :key="question.key"></nq-objective>
        <nq-subjective v-if="question.kind === 2027" class="nq-subjective-wrapper"
          v-bind="{question: question, allStudents: allStudents, showStat: true}"
          :key="question.key"></nq-subjective>
      </template>
    </div>
  </div>
  <div class="nq-view-by-student" v-if="viewMode===2">
    <div class="student-selection">
      <div class="ui middle aligned selection list" v-if="submittedReviewed.length">
        <div class="header header-lb">已批改<span class="note"> 点击标签查看该学生详情</span></div>
        <student-labels
          :orderedResults="submittedReviewed"
          :selectedItem="selectedStudent"
          @selected="s => selectedStudent = s"
          key="submitted-reviewed-students"></student-labels>
      </div>
      <div class="ui middle aligned selection list" v-if="submittedUnreviewed.length">
        <div class="header header-lb">提交未批改<span class="note"> 点击标签查看该学生详情</span></div>
        <student-labels
          :orderedResults="submittedUnreviewed"
          :selectedItem="selectedStudent"
          @selected="s => selectedStudent = s"
          key="submitted-unreviewed-students"></student-labels>
      </div>
      <div class="ui middle aligned selection list" v-if="unsubmitted.length">
        <div class="header header-lb">未提交</div>
        <student-labels
          :orderedResults="unsubmitted" :active="false"
          key="unsubmitted-students"></student-labels>
      </div>
    </div>
    <div class="nq-container" v-if="selectedStudent">
      <div class="student-report-heading">
        <h1 class="student-report-title">{{selectedStudent.name}} 的答题报告</span>
        </h1>
        <p v-if="selectedStudent.score < 0">得分 待批改 /{{cm.totalScore}} 分</p>
        <p v-else>得分 <span class="student-score">{{selectedStudent.score}}</span> /{{cm.totalScore}} 分</p>
      </div>
      <div class="view-setting">
        <span class="ui tooltip link link-primary" data-tooltip="按学生查看，学生若未选中正确答案，则正确答案的选项会以下划线标识出。" data-position="bottom right"> 展示说明&gt;&gt; </span>
      </div>
      <template v-for="question of questions">
        <nq-objective v-if="question.kind === 2026" class="nq-objective-wrapper"
          v-bind="{question: question, allStudents: allStudents, studentId: selectedStudent.id, showUserAnswer: true}"
          :key="question.key"></nq-objective>
        <nq-subjective v-else-if="question.kind === 2027" class="nq-subjective-wrapper"
          v-bind="{question: question, allStudents: allStudents, studentId: selectedStudent.id, showUserAnswer: true}"
          :key="question.key"></nq-subjective>
      </template>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import { mapState, mapGetters } from 'vuex'
import pageDocument from 'components/doc/office'
import pageImage from 'components/doc/image'
import pageVideo from 'components/doc/video'
import pageAudio from 'components/doc/audio'
import InlineSubjectiveResults from 'components/questions/inline-subjective-results'
import BarChart from 'components/questions/bar-chart'
import { getQuestionByData } from 'assets/js/questions/view-mode'
import StudentLabels from 'components/student-labels'
import NqObjective from 'components/questions/nq-objective'
import NqSubjective from 'components/questions/nq-subjective'

export default {
  name: 'new-question',
  props: {
    viewMode: { type: Number, default: 1 },
  },
  data() {
    return {
      nq: null,
      questionsLookUp: {},
      targetKind: 2025,
      questions: [],
      resultModalTarget: null,
      selectedStudent: null,
      showAnalysisFile: false,
      analysisFileInitialized: false,
    }
  },
  created() {
    this.fetchNqQuestions().then(() => { this.attachNqReport() })
  },
  mounted() {
    if (this.submitted && this.submitted.length) {
      this.selectedStudent = this.submitted[0]
    }
  },
  components: {
    pageDocument,
    pageImage,
    pageVideo,
    pageAudio,
    InlineSubjectiveResults,
    BarChart,
    StudentLabels,
    NqObjective,
    NqSubjective,
  },
  computed: {
    hasAnalysisFile() {
      return Boolean(this.nq && this.nq.analysisFileType)
    },
    ...mapState(['cm']),
    ...mapGetters(['allStudents', 'totalStuCount', 'submitted', 'unsubmitted', 'submittedReviewed', 'submittedUnreviewed']),
  },
  watch: {
    cm: function(val, oldVal) {
      if (val.id !== oldVal.id) {
        this.fetchNqQuestions().then(() => { this.attachNqReport() })
      } else {
        this.attachNqReport()
      }
    },
    showAnalysisFile: function(val, oldVal) {
      if (val && !this.analysisFileInitialized) this.analysisFileInitialized = true
    },
    nq: function(val, oldVal) {
      if (val) {
        this.$nextTick(() => {
          $(this.$el.querySelectorAll('.toggle.checkbox')).checkbox()
        })
      }
    },
  },
  methods: {
    getCmpNameByFileType(fileType) {
      if ([2020, 2024, 2028, 2021].includes(fileType)) return 'page-document'
      if (fileType === 2022) return 'page-image'
      if (fileType === 2004) return 'page-video'
      if (fileType === 2023) return 'page-audio'
    },
    fetchNqQuestions() {
      return axios.get('/pad/j/fetch/new_question', { params: { newQuestionId: this.cm.subTargetId } }).then(response => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        } else if (response.data.status === 0) {
          this.questions = []
          this.nq = response.data.nq
          this.questionsLookUp = {}
          for (const questionData of response.data.nq.questions) {
            const q = getQuestionByData(questionData)
            this.questionsLookUp[q.key] = q
            this.questions.push(q)
          }
        }
      })
    },
    attachNqReport() {
      return axios.get('/pad/j/coursematerial/report', { params: { courseMaterialId: this.cm.id, subTargetKind: this.targetKind } }).then((response) => {
        for (const reportData of response.data.report) {
          const key = `key-${reportData.kind}-${reportData.seqId}`
          if (this.questionsLookUp.hasOwnProperty(key)) {
            this.questionsLookUp[key].injectReportData(reportData)
          }
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.file-container {
  max-height: 55vh;
  background-color: @color-light-gray;
  border: @color-black;
  margin-top: -1rem;
  margin-bottom: 1rem;

  position: relative;
  height: 50vh;
  width: 100%;
}
.nq-container {
  font-size: 14px;
  width: 100%;
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  height: 100%;
  max-height: 100%;
  padding: 12px;
}

.nq-view-by-question .nq-container {
  display: flex;
  flex-wrap: wrap;
  .nq-subjective-wrapper {
    width: 100%;
  }
  .nq-objective-wrapper {
    flex: 1;
    min-width: 25rem;
    max-width: 30%;
    margin-right: 1rem;
  }
}

.choice-bar-chart {
  max-width: 12rem;
}

.nq-objective-wrapper, .nq-subjective-wrapper {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.nq-subjective-wrapper {
  border-top: #eee 1px solid;
}
.nq-subjective-wrapper:first-child {
  border-top: none;
}

.student-selection {
  background-color: #fafafa;
  margin-bottom: 2rem;
  padding: .5rem;
  .ui.selection.list .item {
    display: inline-block;
  }
}
.note {
  color: #888;
  font-size: .8em;
}
</style>




