<template>
  <div v-if="cm" class="cm-panel">
    <div class="heading" :class="cm.hasQuestion?'':'dividing'">
      <i class="pad icon" :class="'ft-'+calcFileType(cm)"></i>
      <div class="flex-m">
        <span class="ui header" :title="cm.title">{{cm.title}}</span>
        <span class="ui label fade-label" v-if="courseSelections">{{courseSelections.kvPairs[cm.courseId]}}</span>
        <span class="ui label fade-label">{{ClassKindSelections.kvPairs[cm.classKind]}}</span>
      </div>
     <div v-if="cm.hasQuestion">
       <span class="text-muted" v-if="cm.status === statusReviewed">已完成批改</span>
       <span class="text-muted" v-if="cm.status === statusSubmitted">待批改（已收卷）</span>
       <button class="ui primary tiny button" v-if="cm.status === statusTodo && unsubmitted.length > 0" @click="showModal=true">强制收卷</button>
     </div>
    </div>
    <modal v-if="showModal" @close="showModal=false" :withHeader="false">
      <div slot="body">
        <p class="alert-text">还未提交的学生将不能作答，强制交白卷，确定要收卷吗？</p>
      </div>
      <div slot="footer">
        <button class="ui basic small button" @click="showModal=false">取消</button>
        <button class="ui basic primary small button" @click="stopAnswering($event)">收卷</button>
      </div>
    </modal>
    <div class="cm-content" v-if="cm">
      <document v-if="!cm.hasQuestion"></document>
      <div v-else>
        <div class="ui secondary pointing menu">
          <a class="item" @click="viewMode=1" :class="viewMode===1?'active':''">
            按题目查看
          </a>
          <a class="item" @click="viewMode=2" :class="viewMode===2?'active':''">
            按学生查看
          </a>
          <a class="item" @click="viewMode=3" :class="viewMode===3?'active':''">
            总体情况
          </a>
          <div class="right menu">
            <a class="ui item note">
              <span class="strong-info">{{submittedReviewed.length}}/</span>已批改
              <span class="strong-info">{{submittedUnreviewed.length}}/</span>未批改
              <template v-if="unsubmitted.length">
                <span class="strong-info">{{unsubmitted.length}}/</span>{{cm.status === statusTodo? '未交卷':'强制白卷'}}
              </template>
            </a>
          </div>
        </div>
        <general-stat v-if="viewMode === 3"></general-stat>
        <template v-else>
          <new-question :view-mode="viewMode" v-if="cm.subTargetKind === 2025" :key="cm.id"></new-question>
          <paper :view-mode="viewMode" v-if="cm.subTargetKind === 2018" :key="cm.id"></paper>
          <new-paper :view-mode="viewMode" v-if="cm.subTargetKind === 2035" :key="cm.id"></new-paper>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState, mapMutations, mapGetters } from 'vuex'

import ListFilter from 'components/list-filter'
import Modal from 'components/modal'
import { CM_STATUS_TODO, CM_STATUS_SUBMITTED, CM_STATUS_REVIEWED } from 'assets/js/consts/common'
import GeneralStat from './general-stat'
import NewQuestion from './new-question'
import Document from './document'
import Paper from './paper'
import NewPaper from './new-paper'

const qs = require('qs')

export default {
  name: 'cm-detail',
  data() {
    return {
      viewMode: 1,
      statusTodo: CM_STATUS_TODO,
      statusSubmitted: CM_STATUS_SUBMITTED,
      statusReviewed: CM_STATUS_REVIEWED,
      showModal: false,
    }
  },
  created() {
    this.fetchCmData(this.$route.params.cmId)
  },
  components: { ListFilter, NewQuestion, Paper, NewPaper, Document, Modal, GeneralStat },
  watch: {
    '$route.params.cmId': function(val) {
      this.fetchCmData(val)
      $('html,body').scrollTop(0)
    },
  },
  computed: {
    ...mapState(['cm', 'ClassKindSelections', 'courseSelections']),
    ...mapGetters(['unsubmitted', 'submittedReviewed', 'submittedUnreviewed']),
  },
  methods: {
    calcFileType(cm) {
      if (cm.subTargetKind === 2032) {
        const title = cm.title.toLowerCase()
        const extension = title.split('.').pop()
        if (extension === 'doc' || extension === 'docx') {
          return 2020
        }
        if (extension === 'jpg') {
          return 2022
        }
        if (extension === 'pdf') {
          return 2021
        }
        if (extension === 'mp3') {
          return 2023
        }
        if (extension === 'ppt') {
          return 2024
        }
      }
      return cm.subTargetKind
    },
    fetchCmData(cmId) {
      return axios.get('/pad/j/course-material/detail', { params: { cmId } }).then((response) => {
        this.viewMode = 1
        this.$store.commit('updateCm', response.data.cm)
      })
    },
    stopAnswering(event) {
      $(event.target).addClass('loading')
      axios.post('/pad/j/cm/stop_answering', qs.stringify({ courseMaterialId: this.cm.id }))
        .then(() => {
          this.fetchCmData(this.cm.id)
          this.$nextTick(() => {
            $(event.target).removeClass('loading')
            this.showModal = false
          })
        })
    },
    ...mapMutations(['updateCm']),
  },
}
</script>

<style lang="less" scoped>
  @import "~assets/css/base-variables.less";
  @import "~assets/css/file-icon.less";
  .cm-panel {
    background-color: @color-white;
    box-shadow: 0 2px 1px rgba(0,0,0,0.15);
    min-height: 80vh;
  }

  .heading {
    padding: 1rem;
    margin: 0;
    display: flex;
    align-items: flex-end;

    .pad.icon {
      height: 2.2rem;
      width: 2.2rem;
      padding: .3rem;
      border-radius: 2px;
      margin-right: .8rem;
    }
    .ui.header {
      font-size: 1.6rem;
      vertical-align: bottom;
      font-weight: normal;
      margin: 0;
      display: inline-block;
      max-width: 65%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .flex-m {
      flex: 1;
      width: ~"calc(100% - 13rem)"
    }

  }
  .ui.header.heading, .cm-content {
    padding: 1rem;
  }

  .right.menu .ui.item:last-child {
    padding-right: 0;
  }
  .right.menu .ui.item.note {
    font-size: .9rem;
    color: #777;
    vertical-align: bottom;
    .strong-info {
      margin-left: 1rem;
      color: #333;
      font-size: 1.5rem;
      display: inline-block;
      margin-top: -.2em;
    }
  }
</style>



