<template>
  <div class="list-view">
    <div class="heading">
      <h2 class="container-title" v-if="courseSelections">
        待发送（定时中）的作业
      </h2>
    </div>
    <div class="cm-cards">
      <loader v-if="loading"></loader>
      <div class="content-empty" v-if="!loading && courseMaterials.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>这里什么也没有...</p>
      </div>
      <div class="cm-card" v-for="cm of courseMaterials" :key="cm.id">
        <div class="card-left cm-icon">
          <img :src="cm.coverUrl" alt="作业图标" v-if="cm.coverUrl">
          <i class="pad icon" :class="'ft-'+cm.subTargetKind" v-if="!cm.coverUrl"></i>
        </div>
        <div class="card-right">
          <div class="cm-info">
            <p class="ui header">{{cm.title}}</p>
            <span class="ui label fade-label">{{courseSelections.kvPairs[cm.courseId]}}</span>
            <span class="text-muted">{{ClassKindSelections.kvPairs[cm.classKind]}}</span>
            <span class="text-muted">发布时间：{{cm.publishTime.slice(0, 16)}}</span>
          </div>
          <div class="cm-bottom">
            <span class="text-muted">来源：{{cm.sourceText}}</span>
            <span class="link link-danger pull-right" @click="deleteCm=cm">取消发送</span>
            <span class="pull-right">倒计时：{{displayRemainTime(cm)}}</span>
          </div>
        </div>
      </div>
    </div>
    <pagination class="pagination"
      :totalPage="totalPage" :curPage="page"
      @update:selected="page=>fetchCourseMaterials(page)" v-if="courseMaterials.length"></pagination>
    <modal v-if="deleteCm" class="item-delete" :withHeader="false" @close="deleteCm=null">
      <div slot="body">
        <span class="ui text-center circular label">
          <i class="ui red warning icon"></i>
        </span>
        <p>确定要删除定时作业 <strong>{{deleteCm.title}}</strong> 吗？</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="deleteCm=null">取消</button>
        <button class="ui red button" @click="actionDeleteCm(deleteCm)">删除</button>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
import Modal from 'components/modal'
import Pagination from 'components/pagination'
import { mapState } from 'vuex'
import { PadDateTime } from 'assets/js/utils'
import Loader from 'components/loader'
import 'components/icons/info-circle'
import Icon from 'components/icons/Icon'

const qs = require('qs')

export default {
  name: 'cm-waiting-list',
  data() {
    return {
      deleteCm: null,
      courseMaterials: [],
      page: 1,
      totalPage: 0,
      loading: false,
      previewItem: null,
    }
  },
  components: { Pagination, Loader, Icon, Modal },
  computed: {
    ...mapState(['ClassKindSelections', 'courseSelections']),
  },
  created() {
    this.fetchCourseMaterials()
  },
  methods: {
    displayRemainTime(cm) {
      const now = new Date()
      const padPublishTime = new PadDateTime(cm.publishTime)
      return padPublishTime.formatTimeDelta(now, 'type1')
    },
    fetchCourseMaterials(page = 1) {
      this.loading = true
      axios.get('/pad/j/course-material/waiting-list', { params: { page, pageSize: 15 } })
        .then((response) => {
          $('html,body').scrollTop(0)
          this.courseMaterials = response.data.courseMaterials
          this.page = page
          this.totalPage = response.data.totalPage
          this.loading = false
        })
    },
    actionDeleteCm(cm) {
      axios.post('/pad/j/cm/delete', qs.stringify({ cmId: cm.id })).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, {
            duration: 800,
            position: 'bottom-center',
          })
        } else if (response.data.status === 0) {
          this.$toasted.success('删除成功', {
            duration: 800,
            position: 'bottom-center',
            onComplete: () => {
              const index = this.courseMaterials.indexOf(this.deleteCm)
              this.courseMaterials.splice(index, 1)
              this.deleteCm = null
            },
          })
        }
      })
    },
  },
}
</script>
<style lang="less" scoped>
  .item-delete {
    text-align: center;
    p {
      margin: 0;
      color: #777;
      strong {
        color: #4a4a4a;
        font-weight: bold;
      }
    }
  }
  .item-delete .ui.circular.label {
    margin-bottom: 1rem;
    border: solid 1px #dd4b39;
    border-radius: 3em;
    font-size: 2em;
    width: 1.8em;
    height: 1.8em;
    background-color: white;
    .ui.icon {
      margin: 0;
    }
  }

  .container-title .ui.basic.label.tooltip {
    color: #888;
    padding: .5rem;
    border: none;
    .fa-icon {
      margin-right: 0;
      width: 1.2rem;
      height: 1.2rem;
    }
  }

  .heading, .cm-card {
    border-bottom: 1px solid #e8e8e8;
  }
  .cm-cards {
    min-height: 60vh;
  }

  .cm-card {
    padding: 1.8rem 2rem 1.2rem 2rem;
    display: flex;
    align-items: flex-start;
    .cm-info .ui.header {
      margin-bottom: .3em;
      word-wrap: break-word;
    }
    .cm-bottom {
      font-size: .9rem;
      line-height: 1.8;
    }
    .card-left.cm-icon {
      width: 80px;
      max-width: 80px;
      min-width: 80px;
      text-align: center;
      img {
        border-top: solid 1px #f6f6f6;
        border-right: solid 1px #f6f6f6;
        box-shadow: -3px 3px 2px -2px rgba(0, 0, 0, 0.15);
        border-radius: .3rem;
        max-width: 90%;
        max-height: 5.5rem;
      }
      .pad.icon {
        height: 3.5rem;
        width: 3.5rem;
        padding: .6em;
        border-radius: 3px;
        box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
      }
    }
    .card-right {
      position: relative;
      flex: 1;
      .cm-info {
        flex: 1;
        margin: 0 95px 10px 10px;
        span {
          margin-right: 1rem;
        }
        .ui.header {
          font-size: 1.4rem;
        }
      }
      .cm-bottom {
        margin-left: 10px;
      }
    }
  }
</style>



