import Vue from 'vue'
import Vuex from 'vuex'
import Toasted from 'vue-toasted'
import VueRouter from 'vue-router'
import Navbar from 'components/navbar'
import Course from 'assets/js/course'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import _ from 'lodash'
import { COURSE_STATUS_NORMAL } from 'assets/js/consts/common'
import SidebarFilter from './sidebar-filter'
import CmMainList from './cm-list'
import CmDetail from './cm-detail'
import WaitingList from './waiting-list'

require('./index.less')

Vue.config.productionTip = false

const axios = require('axios')

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'


Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(VueRouter)
Vue.use(Toasted)

const store = new Vuex.Store({
  state: {
    teacher: null,
    ClassKindSelections: {
      title: '作业类型',
      ordered: [0, 3, 2, 1],
      default: 0,
      selected: 0,
      kvPairs: {
        0: '全部作业',
        1: '课后作业',
        2: '课中练习',
        3: '课前预习',
      },
    },
    courseSelections: null,
    courseMaterials: [],
    cmCurPage: 1,
    cmTotalPage: 0,
    cm: null,
    courses: [],
    cmListLoading: false,
    fileType: 0,
    archivedCourses: [],
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
      if (!state.courseSelections && state.teacher.courses.length) {
        const kvPairs = { 0: '所有班级' }
        const ordered = []
        const archivedCourses = []
        for (const courseData of payload.teacher.courses) {
          const course = new Course(courseData)
          state.courses.push(course)
          kvPairs[course.id] = course.name
          if (course.isNormal) {
            ordered.push(course.id)
          } else if (course.isArchived) {
            archivedCourses.push(course)
          }
        }
        state.courseSelections = {
          title: '我的教学班',
          ordered,
          kvPairs,
          default: 0,
          selected: 0,
        }
        state.archivedCourses = archivedCourses
      }
    },
    updateSelectedCourseId(state, selectedCourseId) {
      state.courseSelections.selected = selectedCourseId
    },
    updateSelectedClassKind(state, selectedClassKind) {
      state.ClassKindSelections.selected = selectedClassKind
    },
    updatecmTotalPage(state, cmTotalPage) {
      state.cmTotalPage = cmTotalPage
    },
    updateCmFileType(state, fileType) {
      state.fileType = fileType
    },
    updateCourseMaterials(state, courseMaterials) {
      state.courseMaterials = courseMaterials
    },
    removeCourseMaterial(state, cm) {
      const index = state.courseMaterials.indexOf(cm)
      if (index >= 0) {
        state.courseMaterials.splice(index, 1)
      }
    },
    updateCmCurPage(state, page) {
      state.cmCurPage = page
    },
    updateCm(state, cm) {
      state.cm = cm
      for (const [index, cm_] of state.courseMaterials.entries()) {
        if (cm_.id === cm.id) {
          state.courseMaterials.splice(index, 1, cm)
          break
        }
      }
    },
    changeCmListLoading(state, loading) {
      state.cmListLoading = loading
    },
  },
  getters: {
    school: state => (state.teacher ? state.teacher.school : null),
    classes: state => (state.teacher ? state.teacher.classes : []),
    selectedClassKind: state => (state.ClassKindSelections ? state.ClassKindSelections.selected : 0),
    selectedCourseId: state => (state.courseSelections ? state.courseSelections.selected : null),
    submittedReviewed: state => (state.cm ? state.cm.submittedReviewed : []),
    submittedUnreviewed: state => (state.cm ? state.cm.submittedUnreviewed : []),
    submitted(state) {
      if (!state.cm) return []
      return _.concat(state.cm.submittedReviewed, state.cm.submittedUnreviewed)
    },
    unsubmitted: state => (state.cm ? state.cm.unsubmitted : []),
    allStudents(state) {
      if (!state.cm) return null
      const students = {}
      for (const group of [state.cm.submittedReviewed, state.cm.submittedUnreviewed, state.cm.unsubmitted]) {
        for (const s of group) {
          students[s.id] = s
        }
      }
      return students
    },
    totalStuCount(state) {
      if (!state.cm) return 0
      let len = 0
      for (const group of [state.cm.submittedReviewed, state.cm.submittedUnreviewed, state.cm.unsubmitted]) {
        len += group.length
      }
      return len
    },
  },
  actions: {
    fetchCourseMaterials({ state, getters, commit }) {
      commit('changeCmListLoading', true)
      commit('updateCourseMaterials', [])
      const params = {
        page: state.cmCurPage,
        classKind: getters.selectedClassKind,
        courseId: getters.selectedCourseId,
        fileType: state.fileType,
      }
      axios.get('/pad/j/course-material/list', { params })
        .then((response) => {
          commit('updateCourseMaterials', response.data.courseMaterials)
          commit('updatecmTotalPage', response.data.totalPage)
          $('html,body').scrollTop(0)
          commit('changeCmListLoading', false)
        })
    },
  },
})

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/course-material' : '/static/w/pages/course-material.html',
  routes: [
    {
      path: '',
      name: 'cm-main-list',
      components: { sidebar: SidebarFilter, 'main-content': CmMainList },
    },
    {
      path: '/waiting',
      name: 'cm-waiting-list',
      components: { sidebar: SidebarFilter, 'main-content': WaitingList },
      props: {
        sidebar: { sidebarView: true },
      },
    },
    {
      path: '/:cmId',
      name: 'cm-detail',
      components: { sidebar: CmMainList, 'main-content': CmDetail },
      props: {
        sidebar: { sidebarView: true },
      },
    },
  ],
  linkActiveClass: 'active',
})


router.beforeEach((to, from, next) => {
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then((response) => {
      const teacher = response.data.teacher
      store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
      next()
    })
  } else {
    next()
  }
})

new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  store,
  router,
  data: {},
  components: { Navbar },
  created() {
    this.$store.watch((state, getters) => getters.selectedClassKind, () => {
      this.$store.commit('updateCmCurPage', 1)
      this.$store.dispatch('fetchCourseMaterials')
    })
    this.$store.watch((state, getters) => getters.selectedCourseId, () => {
      this.$store.commit('updateCmCurPage', 1)
      this.$store.dispatch('fetchCourseMaterials')
    })
    this.$store.watch(state => state.cmCurPage, () => {
      this.$store.dispatch('fetchCourseMaterials')
    })
    this.$store.watch(state => state.fileType, () => {
      this.$store.commit('updateCmCurPage', 1)
      this.$store.dispatch('fetchCourseMaterials')
    })
  },
})



