<template>
<div class="document-view">
  <div class="file-container" :class="currentView.type + '-view'">
    <component :is="currentView.type" v-bind="currentView.props" :key="currentView.key"></component>
  </div>
  <template v-if="submitted.length">
    <h3 class="ui header">已查看（{{submitted.length}}）</h3>
    <student-labels :orderedStudents="submitted" key="submitted-students"></student-labels>
  </template>
  <template v-if="unsubmitted.length">
    <h3 class="ui header">未查看（{{unsubmitted.length}}）</h3>
    <student-labels :orderedStudents="unsubmitted" :active="false" key="unsubmitted-students"></student-labels>
  </template>
</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import pageDocument from 'components/doc/office'
import pageImage from 'components/doc/image'
import pageVideo from 'components/doc/video'
import pageAudio from 'components/doc/audio'
import StudentLabels from 'components/student-labels'

export default {
  name: 'document',
  data() {
    return {}
  },
  created() {
  },
  components: {
    pageDocument, pageImage, pageVideo, pageAudio, StudentLabels,
  },
  computed: {
    currentView() {
      let type = 'page-document'
      switch (this.cm.subTargetKind) {
        case 2020:
        case 2024:
        case 2028:
        case 2021:
        case 2032:
          type = 'page-document'; break
        case 2022:
          type = 'page-image'; break
        case 2004:
          type = 'page-video'; break
        case 2023:
          type = 'page-audio'; break
        default:
          return null
      }
      return {
        type,
        props: { fileUrl: this.cm.docUrl },
        key: `${this.cm.subTargetKind}${this.cm.subTargetId}`,
      }
    },
    ...mapState(['cm']),
    ...mapGetters(['unsubmitted', 'submitted']),
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.document-view {
  margin-top: -1rem;
}
.file-container {
  height: 55vh;
  background-color: @color-light-gray;
  border: @color-black;
  position: relative;
}
.file-container.page-audio-view {
  height: 5rem;
}

.header.ui {
  border-left: .3rem solid @color-primary;
  padding-left: .5rem;
  font-size: 1rem;
  font-weight: normal;
  margin-top: 1rem;
}
</style>




