<template>
  <div class="list-view" :class="sidebarView?'side-list':'main-list'">
    <div class="heading">
      <button v-if="sidebarView" class="link link-primary"
        @click="$router.push({name: 'cm-main-list'})">&lt;&lt;返回作业批改首页</button>
      <h2 class="container-title" v-if="courseSelections">
        {{courseSelections.kvPairs[courseSelections.selected]}}
        {{ClassKindSelections.kvPairs[ClassKindSelections.selected]}}
        <label class="pull-right ui basic label tooltip" data-tooltip="发布12小时内的作业可删除。若删除作业，该作业上已作答学生的数据也会被清除。" data-position="top right" v-if="!sidebarView">
          <icon name="info-circle"></icon>
        </label>
      </h2>
      <div class="pill-filter" v-if="!sidebarView">
        类型：
        <span class="filter-label" :class="fileType===0?'active':''" @click="$store.commit('updateCmFileType', 0)">全部</span>
        <span class="filter-label" :class="fileType===2035?'active':''" @click="$store.commit('updateCmFileType', 2035)">试题组</span>
        <span class="filter-label" :class="fileType===2025?'active':''" @click="$store.commit('updateCmFileType', 2025)">互动资料</span>
        <span class="filter-label" :class="fileType===2030?'active':''" @click="$store.commit('updateCmFileType', 2030)">文档/图片/视频/音频</span>
        <span class="filter-label" :class="fileType===2018?'active':''" @click="$store.commit('updateCmFileType', 2018)">试题组（旧）</span>
      </div>
    </div>
    <div class="cm-cards">
      <loader v-if="cmListLoading"></loader>
      <div class="content-empty" v-if="!cmListLoading && courseMaterials.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>这里什么也没有...</p>
      </div>
      <div class="cm-card" v-for="cm of courseMaterials" :key="cm.id">
        <div class="card-left cm-icon">
          <img :src="cm.coverUrl" alt="作业图标" v-if="!sidebarView && cm.coverUrl">
          <i class="pad icon" :class="'ft-'+calcFileType(cm)" v-if="!sidebarView && !cm.coverUrl"></i>
        </div>
        <div class="card-right">
          <div class="cm-info">
            <p class="ui header" @click="showCmDetail(cm)"><i class="pad icon" :class="'ft-'+calcFileType(cm)" v-if="sidebarView"></i>{{cm.title}}</p>
            <span :class="sidebarView?'text-muted':'ui label'" class="fade-label">{{courseSelections.kvPairs[cm.courseId]}}</span>
            <span class="text-muted" v-if="!sidebarView" >{{ClassKindSelections.kvPairs[cm.classKind]}}</span>
            <span class="text-muted" v-if="!sidebarView">{{cm.publishTime.slice(0, 16)}}</span>
          </div>
          <div class="cm-stat" :class="sidebarView?'text-muted':'ui tiny statistic'">
            <span class="value">
              {{cm.nSubmitted}}/{{cm.nStudents}}
            </span>
            <span class="label">
              {{cm.hasQuestion ? '已提交' : '已查看'}}
            </span>
          </div>
          <div class="cm-bottom">
            <span class="text-muted" v-if="!sidebarView">来源：{{cm.sourceText}}</span>
            <span class="pull-right" v-if="cm.hasQuestion">
              <span class="ui label text-danger fade-label" v-if="needReview(cm)">可批改</span>
              <span class="ui label text-danger fade-label" v-if="cm.status === statusTodo">{{cmStatuses[cm.status]}}</span>
              <span class="ui label text-muted fade-label" v-else>{{cmStatuses[cm.status]}}</span>
            </span>
            <span class="link link-danger pull-right" v-if="cm.canDelete && !sidebarView" @click="deleteCm=cm">删除作业</span>
          </div>
        </div>
      </div>
    </div>
    <pagination class="pagination"
      :totalPage="cmTotalPage" :curPage="cmCurPage"
      :size="sidebarView?'small':''"
      @update:selected="updateCmCurPage" v-if="courseMaterials.length"></pagination>
    <modal v-if="deleteCm" class="item-delete" :withHeader="false" @close="deleteCm=null">
      <div slot="body">
        <span class="ui text-center circular label">
          <i class="ui red warning icon"></i>
        </span>
        <p>确定要删除作业 <strong>{{deleteCm.title}}</strong> 吗？</p>
        <p>删除后不可恢复，且该作业上已作答学生的数据也会被清除。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="deleteCm=null">取消</button>
        <button
          class="ui red button"
          :class="deleteLoading?'loading':''"
          @click="actionDeleteCm(deleteCm)">删除</button>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
import Modal from 'components/modal'

import Pagination from 'components/pagination'
import { mapState, mapMutations } from 'vuex'
import { CM_STATUS_TODO, CM_STATUSES, CM_STATUS_SUBMITTED } from 'assets/js/consts/common'
import Loader from 'components/loader'
import 'components/icons/info-circle'
import Icon from 'components/icons/Icon'

const qs = require('qs')

export default {
  name: 'cm-list',
  props: {
    sidebarView: { type: Boolean, default: false },
  },
  data() {
    return {
      statusTodo: CM_STATUS_TODO,
      cmStatuses: CM_STATUSES,
      deleteCm: null,
      deleteLoading: false,
    }
  },
  components: { Pagination, Loader, Icon, Modal },
  computed: {
    ...mapState([
      'ClassKindSelections', 'courseSelections', 'courseMaterials', 'cmListLoading', 'fileType',
      'cmTotalPage', 'cmCurPage']),
  },
  methods: {
    updateFileType(val) {
      this.$store.commit('updateCmFileType', val)
    },
    calcFileType(cm) {
      if (cm.subTargetKind === 2032) {
        const title = cm.title.toLowerCase()
        const extension = title.split('.').pop()
        if (extension === 'doc' || extension === 'docx') {
          return 2020
        }
        if (extension === 'jpg') {
          return 2022
        }
        if (extension === 'pdf') {
          return 2021
        }
        if (extension === 'mp3') {
          return 2023
        }
        if (extension === 'ppt') {
          return 2024
        }
      }
      return cm.subTargetKind
    },
    showCmDetail(cm) {
      this.$router.push({ name: 'cm-detail', params: { cmId: cm.id } })
    },
    needReview(cm) {
      if (cm.status === CM_STATUS_SUBMITTED) return cm.nStudents > cm.nReviewed
      if (cm.status === CM_STATUS_TODO) return cm.nSubmitted > cm.nReviewed
      return false
    },
    actionDeleteCm(cm) {
      this.deleteLoading = true
      axios.post('/pad/j/cm/delete', qs.stringify({ cmId: cm.id })).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'bottom-center',
          })
          this.deleteLoading = false
        } else if (response.data.status === 0) {
          this.$toasted.success('删除成功', {
            duration: 1000,
            position: 'bottom-center',
            onComplete: () => {
              this.deleteCm = null
              this.$store.commit('removeCourseMaterial', cm)
              this.deleteLoading = false
            },
          })
        } else {
          this.deleteLoading = false
        }
      })
    },
    ...mapMutations(['updateCmCurPage']),
  },
}
</script>

<style lang="less" scoped>
  @import "~assets/css/base-variables.less";
  .item-delete {
    text-align: center;
    p {
      margin: 0;
      color: #777;
      strong {
        color: #4a4a4a;
        font-weight: bold;
      }
    }
  }
  .item-delete .ui.circular.label {
    margin-bottom: 1rem;
    border: solid 1px #dd4b39;
    border-radius: 3em;
    font-size: 2em;
    width: 1.8em;
    height: 1.8em;
    background-color: white;
    .ui.icon {
      margin: 0;
    }
  }

  .container-title .ui.basic.label.tooltip {
    color: #888;
    padding: .5rem;
    border: none;
    .fa-icon {
      margin-right: 0;
      width: 1.2rem;
      height: 1.2rem;
    }
  }

  // common
  .heading, .cm-card {
    border-bottom: 1px solid #e8e8e8;
  }
  .cm-cards {
    min-height: 60vh;
  }
  .cm-info .ui.header {
    margin-bottom: .3em;
    cursor: pointer;
    word-wrap: break-word;
  }

  .cm-bottom {
    font-size: .9rem;
    line-height: 1.8;
  }

  .cm-info .ui.header {
    &:hover, &:focus, &:active {
      color: @color-blue !important;
      transition: all 0.2s ease;
      text-decoration: underline;
    }
  }

  // conditional
  .side-list .cm-card {
    padding: 1rem .8rem;
    .cm-info {
      .ui.header {
        font-size: 1.1rem;
        font-weight: normal !important;
      }
      span {
        margin-right: 1rem;
      }
      .pad.icon {
        width: 1.5rem;
        height: 1.5rem;
        border-radius: 3px;
        padding: 3px;
      }
    }
    .text-muted, .text-danger {
      font-size: 12px;
    }
    .card-right {
      position: relative;
      .cm-bottom {
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }
    .cm-stat {
      margin-top: .5em;
      padding-bottom: 2px;
    }
  }
  .side-list .heading {
    padding: 1rem .8rem;
    .container-title {
      font-size: 1.2rem;
      margin-bottom: 0;
    }
    .link {
      font-size: 1rem;
    }
  }

  .main-list .cm-card {
    padding: 1.8rem 2rem 1.2rem 2rem;
    display: flex;
    align-items: flex-start;
  }
  .main-list .cm-card .card-left.cm-icon {
    width: 80px;
    max-width: 80px;
    min-width: 80px;
    text-align: center;
    img {
      border-top: solid 1px #f6f6f6;
      border-right: solid 1px #f6f6f6;
      box-shadow: -3px 3px 2px -2px rgba(0, 0, 0, 0.15);
      border-radius: .3rem;
      max-width: 90%;
      max-height: 5.5rem;
    }
    .pad.icon {
      height: 3.5rem;
      width: 3.5rem;
      padding: .6em;
      border-radius: 3px;
      box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
    }
  }
  .main-list .cm-card .card-right {
    position: relative;
    flex: 1;
    .cm-info {
      flex: 1;
      margin: 0 95px 10px 10px;
      span {
        margin-right: 1rem;
      }
      .ui.header {
        font-size: 1.4rem;
      }
    }
    .cm-stat {
      position: absolute;
      right: 0;
      top: 0;
      margin: 0;
      &.ui.statistic .value {
        font-size: 1.6rem !important;
      }
      &.ui.statistic .label {
        font-size: .9rem !important;
      }
    }
    .cm-bottom {
      margin-left: 10px;
    }
  }



</style>



