<template>
  <div class="ui middle aligned selection list">
    <div class="ui header"><icon name="filter"></icon><div class="content">{{selections.title}}</div></div>
    <div class="item"
      v-for="key in selections.ordered" :key="key"
      :class="key === selections.selected? 'active':''"
      @click="$emit('update:selected', key)">
      <div>{{selections.kvPairs[key]}}</div>
    </div>
  </div>
</template>

<script>
import 'components/icons/filter'
import Icon from 'components/icons/Icon'
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'list-filter',
  data() {
    return {}
  },
  props: {
    /*
    exampleSelections: {
      title: '作业类型',
      ordered: [0, 3, 2, 1],
      default: 0,
      selected: 0,
      kvPairs: {0: '全部', 1: '课后作业', 2: '课中练习', 3: '课前预习' }
    }
    */
    selections: {
      type: Object,
      validator(val) {
        return val.ordered.length > 0
      },
    },
  },
  components: { Icon },
  computed: {
    ...mapState(['teacher']),
    ...mapGetters(['school', 'classes', 'courses']),
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.ui.list {
  margin-bottom: 2rem;
  .ui.header {
    color: @color-light-black;
    font-size: 1.2rem;
    margin-bottom: .5rem;
    .icon {
      width: 1.25rem;
      font-size: .95em;
    }
  }
  .item {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

</style>



