<template>
  <div class="sidebar-filter">
    <button class="link link-primary" @click="$router.push({ name: 'cm-waiting-list', force: true})">待发送（定时中）的作业&gt;&gt;</button>
    <router-link to="/waiting" tag="button" class="link link-primary">

    </router-link>
    <list-filter
      v-if="ClassKindSelections"
      :selections="ClassKindSelections"
      @update:selected="updateSelectedClassKind"></list-filter>
    <list-filter
      v-if="courseSelections"
      :selections="courseSelections"
      @update:selected="updateSelectedCourseId"></list-filter>

    <template v-if="archivedCourses.length">
      <button
        v-if="archiveCoursesFolded"
        @click="archiveCoursesFolded = false"
        class="link link-default">查看已归档的历史班级 <i class="ui icon angle down"></i> </button>
      <button
        v-else
        @click="archiveCoursesFolded = true"
        class="link link-default">收起已归档的历史班级 <i class="ui icon angle up"></i> </button>

      <div class="ui middle aligned selection list" v-show="!archiveCoursesFolded">
        <div
          class="item"
          v-for="c of archivedCourses" :key="c.id"
          :class="c.id === selectedCourseId? 'active':''"
          @click="updateSelectedCourseId(c.id)">
          <div>{{c.name}}</div>
        </div>
      </div>

    </template>

  </div>
</template>

<script>
import ListFilter from 'components/list-filter'
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'sidebar-filter',
  data() {
    return {
      archiveCoursesFolded: true,
    }
  },
  components: {
    'list-filter': ListFilter,
  },
  computed: {
    ...mapState(['ClassKindSelections', 'courseSelections', 'archivedCourses']),
    ...mapGetters(['selectedCourseId']),
  },
  methods: {
    updateSelectedClassKind(val) {
      if (this.$router.currentRoute.name === 'cm-waiting-list') {
        this.$router.push({ name: 'cm-main-list' })
      }
      this.$store.commit('updateSelectedClassKind', val)
    },
    updateSelectedCourseId(val) {
      if (this.$router.currentRoute.name === 'cm-waiting-list') {
        this.$router.push({ name: 'cm-main-list' })
      }
      this.$store.commit('updateSelectedCourseId', val)
    },
  },
}
</script>

<style lang="less" scoped>
  .sidebar-filter {
    padding: 1.5rem .8rem;
    button.link {
      text-align: right;
      font-size: 1rem;
    }
  }
</style>



